import React from "react";
import { Link } from "react-router-dom";
import { SHOP_PAGE, CHECKOUT_PAGE } from "config";

import PageHeader from "components/PageHeader";
import Title from 'components/Title';
import CartManager from 'components/CartManager';
import CartTotal from './CartTotal';

import './style.scss';
export default function CartPage() {

    return (
        <main>
            <section className="clear--padding">
                <div className="container">
                    <PageHeader title='Cart' slug='' last='Cart' />
                </div>
            </section>
            <section>
                <div className="container">
                    <div className="gap"></div>
                    <Title title='SHOPPING CART' />
                    <div className="gap"></div>
                    <div className="flex flex--gap">
                        <div className="col-sm-12 col-lg-8">
                            <CartManager />
                            <div className="gap"></div>
                            <div className="pd bg-light">
                                <div className="flex x--between y--center cart-nav">
                                    <Link className="btn clear--background" to={SHOP_PAGE}>
                                        <i className="fa fa-arrow-left pi--10"></i> Continue shopping
                                    </Link>
                                    <Link className="btn black--border clear--background" to={CHECKOUT_PAGE}>
                                        Procceed to checkout
                                        <i className="fa fa-arrow-right pi--10"></i>
                                    </Link>
                                </div>
                            </div>
                        </div>
                        <div className="col-sm-12 col-lg-4">
                            <CartTotal />
                        </div>
                    </div>
                </div>
            </section>

        </main>
    )
}