import { useSelector, useDispatch } from "react-redux";
import { addItem, reduceItem, removeItem } from 'store/cart';

import './style.scss';
export default function CartManager() {
    const cart = useSelector(state => state.cart);
    const dispatch = useDispatch();

    // Xử lý sự kiện tăng số lượng sản phẩm, thêm 1 sản phẩm trùng id với amoun bằng 1
    const incressProductAmount = (id, price) => {
        dispatch(addItem({
            id: id,
            price: price,
            amount: 1
        }))
    }
    // Xử lý sự kiện giảm 1 sản phẩm
    const descressProductAmount = (id) => {
        dispatch(reduceItem(id));
    }
    // Xử lý sự kiện loại bỏ sản phẩm
    const removeProduct = (id) => {
        dispatch(removeItem(id));
    }

    return (
        <div>
            <table className="table scroll border cart-manager">
                <thead>
                    <tr className="align--center">
                        <th>IMAGE</th>
                        <th>PRODUCT</th>
                        <th>PRICE</th>
                        <th>QUANTITY</th>
                        <th>TOTAL</th>
                        <th>.</th>
                    </tr>
                </thead>
                <tbody>
                    {cart.items.length > 0 && (
                        cart.items.map((p, i) => {
                            return (
                                <tr key={i}>
                                    <td>
                                        <div className="product-img"><img src={p.img} /></div>
                                    </td>
                                    <td className="align--center">
                                        {p.name}
                                    </td>
                                    <td className="align--right">
                                        {new Intl.NumberFormat("de-DE").format(p.price) + 'đ'}
                                    </td>
                                    <td className="align--right">
                                        <div className="flex flex--nowrap">
                                            <button className="btn btn--micro" onClick={() => descressProductAmount(p.id)}>
                                                <i className="fa fa-caret-left"></i>
                                            </button>
                                            <span className="pi--10">{p.amount}</span>
                                            <button className="btn btn--micro" onClick={() => incressProductAmount(p.id, p.price)}>
                                                <i className="fa fa-caret-right"></i>
                                            </button>
                                        </div>
                                    </td>
                                    <td className="align--right">
                                        {new Intl.NumberFormat("de-DE").format(p.price * p.amount) + 'đ'}
                                    </td>
                                    <td>
                                        <button className="btn btn--micro" onClick={() => removeProduct(p.id)}>
                                            <i className="fa fa-trash"></i>
                                        </button>
                                    </td>
                                </tr>
                            )
                        })
                    )}
                </tbody>
            </table>
            {cart.items.length === 0 && (
                <div className="pd border align--center">Chưa có sản phẩm nào trong giỏ hàng</div>
            )}
        </div>
    )
}