import { useSelector } from "react-redux"
import { Link } from "react-router-dom";
import { CHECKOUT_PAGE } from "config";

export default function CartTotal() {
    const cart = useSelector(state => state.cart);

    return (
        <div className="pd pi--30 cart-total">
            <h5>CART TOTAL</h5>
            <div className="gap--10"></div>
            <ul className="alt">
                <li className="flex x--between y--center">
                    <div><h6>SUBTOTAL</h6></div>
                    <div><span>{new Intl.NumberFormat("de-DE").format(cart.totalPay) + ' VNĐ'}</span></div>
                </li>
                <li className="divider"></li>
                <li className="flex x--between y--center">
                    <div><h6>TOTAL</h6></div>
                    <div><h6>{new Intl.NumberFormat("de-DE").format(cart.totalPay) + ' VNĐ'}</h6></div>
                </li>
            </ul>
            <div className="gap"></div>
            <div className="cart-total__coupon">
                <input type='text' className="input" placeholder="Enter your coupon" />
                <div className="gap--10"></div>
                <button className="btn black apply-coupon">
                    <i className="fa fa-gift pi--10"></i>
                    Apply Coupon
                </button>
            </div>
            <div className="gap--10"></div>
            <div className="divider"></div>
            <div className="gap--10"></div>
            <Link className="btn black block" to={CHECKOUT_PAGE}>
                Procceed to checkout
            </Link>
            <div className="gap"></div>
        </div>
    )
}